@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@400;800&display=swap);
body {
  margin: 0;
  font-family: 'Nunito Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav {
  background: rgb(56, 57, 59);
  width: 200px;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  margin: 0;
  padding: 0;
  color: white;
  list-style: none;
  line-height: 3em;
  font-size: 11pt;
}

.nav li {
  cursor: pointer;
  padding: 0 20px;
}

.nav li:nth-child(n+2):hover {
  background: black;
}

.new {
  border: 2px solid white;
  background: none;
  color: white;
  width: 100%;
  border-radius: 20px;
  font-size: 12pt;
  line-height: 24pt;
  margin: 40px 0 20px 0;
  outline : none;
  cursor: pointer;
}

.plus {
  font-size: 24pt;
  line-height: 24pt;
  padding: 0 10px 0 0;
  vertical-align: middle;
}

.label {
  vertical-align: middle;
}
.start {
  width: calc(100% - 200px);
  height: 100%;
  position: fixed;
  top: 0;
  left: 200px;
  padding: 80px 100px;
}

h1 {
  font-weight: 800;
  font-size: 36pt;
  margin: 0;
}

h4 {
  font-weight: 400;
  font-size: 14pt;
}

.start-project:hover {
  background: rgb(61, 124, 33);
}

input, textarea {
  width: calc(100% - 20px);
  padding: 10px;
  border: 1px solid #ccc;
  font-family: 'Nunito Sans', 'Helvetica Neue', sans-serif;
  font-size: 12pt;
}

textarea {
  height: 100px;
}

.scrim {
  background: black;
  opacity: 0;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  pointer-events: none;
}

.side-panel {
  background: white;
  width: 600px;
  height: 100%;
  position: fixed;
  right: -640px;
  top: 0;
  border-radius: 5px;
  box-shadow: -5px -10px 5px rgba(0, 0, 0, 0);
  padding: 20px;
  -webkit-animation: slidein 1s forwards;
          animation: slidein 1s forwards;
}

h2 {
  font-size: 14pt;
  margin: 0 0 20px 0;
}

h4 {
  font-size: 12pt;
  margin-bottom: 5px;
}

.show .side-panel {
  -webkit-animation: slidein 0.33s normal forwards;
          animation: slidein 0.33s normal forwards;
}

.hide .side-panel {
  -webkit-animation: slideout 0.33s normal forwards;
          animation: slideout 0.33s normal forwards;
}

.show .scrim {
  -webkit-animation: fadein 0.33s normal forwards;
          animation: fadein 0.33s normal forwards;
}

.hide .scrim {
  -webkit-animation: fadeout 0.33s normal forwards;
          animation: fadeout 0.33s normal forwards;
}

@-webkit-keyframes slidein {
  100% { 
    right: 0;
    box-shadow: -5px -10px 5px rgba(0, 0, 0, 0.15);
  }
}

@keyframes slidein {
  100% { 
    right: 0;
    box-shadow: -5px -10px 5px rgba(0, 0, 0, 0.15);
  }
}

@-webkit-keyframes slideout {
  100% { 
    right: -640px;
    box-shadow: -5px -10px 5px rgba(0, 0, 0, 0);
  }
}

@keyframes slideout {
  100% { 
    right: -640px;
    box-shadow: -5px -10px 5px rgba(0, 0, 0, 0);
  }
}

@-webkit-keyframes fadein {
  100% { 
    opacity: 0.65;
  }
}

@keyframes fadein {
  100% { 
    opacity: 0.65;
  }
}

@-webkit-keyframes fadeout {
  100% { 
    opacity: 0;
  }
}

@keyframes fadeout {
  100% { 
    opacity: 0;
  }
}

.add {
  font-weight: 800;
  color: rgb(85, 158, 54);
}

.add-customer {
  outline: 1px solid rgb(187, 190, 197);
  border: 2px solid white;
  background: rgb(236, 238, 241);
  padding: 5px;
  margin-left: 1px;
  display: none;
  cursor: pointer;
}

.add-customer.show {
  display: block;
}

.new-customer {
  border: 1px solid #aaa;
  border-radius: 3px;
  width: 320px;
  padding: 20px;
  display: none;
  margin-top: 5px;
}

.new-customer.show {
  display: block;
}

.divider {
  background: black;
  height: 2px;
  border: 0;
}

.new-customer p {
  margin-bottom: 0;
}

.save-container {
  display: flex;
  align-items: flex-end;
  direction: rtl;
  width: 100%;
}

.company {
  position: fixed;
  top: 0;
  left: 200px;
  background: rgb(244, 245, 248);
  width: calc(100% - 280px);
  height: 100%;
  padding: 20px 40px;
}

.company.hide {
  display: none;
}

.company h2 {
  margin: 10px 0;
}

.company h3 {
  margin: 0;
  font-size: 12pt;
  font-weight: 200;
}

.company h4 {
  font-size: 11pt;
  color: #ccc;
  margin: 0;
}

a {
  text-decoration: none;
  color: rgb(54, 118, 192);
}

a:hover {
  text-decoration: underline;
}

.card {
  border: 1px solid rgb(228, 230, 233);
  border-radius: 3px;
  width: 100%;
  height: 80px;
  margin: 20px 0;
  background: white;
  box-shadow: 0 1px #bbb;
}

.mini-cards {
  display: flex;
}

.mini-card {
  border: 1px solid rgb(228, 230, 233);
  border-radius: 3px;
  margin: 10px;
  padding: 10px;
  background: white;
  box-shadow: 0 1px #bbb;
  text-align: center;
  width: 33%;
  position: relative;
}

.amount {
  font-size: 24pt;
  font-weight: 400;
}

.type {
  text-transform: uppercase;
  color: #888;
  font-size: 10pt;
  font-weight: 800;
}

.green {
  color: rgb(84, 158, 53);
}

.teal {
  color: rgb(61, 130, 128);
}

.circle {
  position: absolute;
  top: 22px;
  width: 40px;
  height: 40px;
  text-align: center;
  vertical-align: middle;
  border-radius: 20px;
  background:  rgb(244, 245, 248);
  z-index: 1;
  font-size: 28pt;
  color: #888;
  font-weight: 800;
  line-height: 40px;
}

.circle.left {
  left: -30px;
}

.circle.right {
  right: -30px;
}

.company-nav {
  list-style: none;
  display: flex;
  margin: 0;
  padding: 50px 10px;
}

.company-nav li {
  padding: 5px 0;
  margin-right: 50px;
  font-size: 12pt;
  color: #555;
}

.company-nav li.selected {
  border-bottom: 2px solid rgb(84, 158, 53);
  color: black;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
}

.multi-scrim {
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
}

.scrim1 {
  top: 0;
  left: 0;
  width: 100%;
  height: 270px;
}

.scrim2 {
  top: 270px;
  left: 0;
  width: 230px;
  height: calc(100% - 270px);
}

.scrim3 {
  top: 270px;
  right: 0;
  width: 30px;
  height: calc(100% - 270px);
}

.scrim4 {
  top: 270px;
  left: 340px;
  width: calc(100% - 340px - 30px);
  height: 80px;
}

.scrim5 {
  bottom: 0;
  left: 230px;
  width: calc(100% - 230px - 30px);
  height: 100px;
}

.overlay.show {
  display: block;
}

.info {
  position: fixed;
  top: 200px;
  left: 360px;
  width: 400px;
  border: 1px solid rgb(228, 230, 233);
  border-radius: 3px;
  padding: 20px;
  background: white;
  box-shadow: 0 1px #bbb;
  z-index: 100;
}

.info h2 {
  margin-top: 0;
}

.info p {
  margin-bottom: 0;
}

.info .close {
  position: absolute;
  top: 5px;
  right: 15px;
  font-size: 24pt;
  color: #888;
  cursor: pointer;
}
button {
  border: 1px solid #333;
  background: white;
  border-radius: 24px;
  font-size: 12pt;
  line-height: 24pt;
  margin: 20px 0;
  outline : none;
  cursor: pointer;
  padding: 2px 20px;
  font-weight: 800;
  min-width: 100px;
}

button.green {
  background: rgb(85, 158, 54);
  color: white;
  border: none;
}
